* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: url(https://cdn.custom-cursor.com/db/7264/32/material-space-cursor.png),
    default;

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: #555;
    border-radius: 8px;
  }

  &::-webkit-scrollbar {
    width: 8px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
span,
p,
b,
a {
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
  color: #1b1b1b;
}

body {
  padding-left: 60px;
  background: linear-gradient(-45deg, #00084e, #004e92, #6a0572);
  overflow: hidden;
}

ul {
  list-style: none;
  display: flex;
}

nav {
  width: 60px;
  height: 100svh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  border-right: 1px solid #eee;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);

  ul {
    flex-direction: column;
    align-items: center;
    gap: 25px;

    li {
      width: 45px;
      height: 45px;
      display: flex;
      align-items: center;
      justify-content: center;

      a {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: url(https://cdn.custom-cursor.com/db/7263/32/material-space-pointer.png),
          pointer;

        path {
          stroke: #fff;
        }

        &[class*='active'] {
          background: #252a34;
          pointer-events: none;
        }
      }

      svg {
        pointer-events: none;
        transition: 0.1s ease-in-out;

        &:hover {
          transform: scale(1.07);
        }
      }
    }
  }
}

header {
  position: fixed;
  top: 0;
  right: 0;
  padding: 20px;

  ul {
    width: 100%;
    height: max-content;
    align-items: center;
    gap: 15px;

    a {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: url(https://cdn.custom-cursor.com/db/7263/32/material-space-pointer.png),
        pointer;

      &:hover svg {
        transform: scale(1.07);
        opacity: 1;
      }

      svg {
        pointer-events: none;
        width: 100%;
        height: 100%;
        transition: 0.1s ease-in-out;
        opacity: 0.8;
        filter: drop-shadow(0 0 1px #fff);
      }
    }
  }

  @media (max-width: 768px) {
    padding: 15px;

    ul {
      gap: 10px;
    }
  }
}
